.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background-color: white;
    z-index: 100;
    width: 100%;
    top: 0;
}

.header__icon {
    object-fit: contain;
    height: 80px;
    margin-left: 30px;
    padding: 10px;
}

.header__center {
    display: flex;
    flex: 1;
    align-items: center;
    max-width: fit-content;
    padding: 10px;
    height: 30px;
    border: 1px solid lightgray;
    border-radius: 999px;
    margin-left: 170px;
}

.header__center > input {
    border: none;
    width: 400px; 
    padding: 10px;
    outline-width: 0;
}

.header__right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 22vw;
    margin-right: 30px;
}