.banner {
    height: 60vh;
    position: relative;
    background: url("https://www.dsclaw.co.za/wp-content/uploads/2020/11/claiming-from-road-accident-fund-without-a-lawyer.jpg") center center;
}

.banner__info {
    background-color: black;
    color: white;
    padding-top: 25vh;
    padding-left: 50px;
    padding-right: 50px;
    padding-bottom: 40px;
    width: 300px;
}

.banner__info > h1 {
    margin-top: -40px;
}

.banner__info > button {
    background-color: #ff7779;
    color: white;
    text-transform: inherit;
    margin-top: 30px;
    font-weight: 600;
    font-size: 30px;
}

.banner__info > h5 {
    margin-top: 10px;
}

.banner__info > button:hover {
    background-color: white;
    color: #ff7779;
}

.banner__search {
    display: flex;
    flex-direction: column;
}

.banner__searchButton {
    background-color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    color: #ff7779 !important;
}