.searchPage__info {
    position: flex;
    text-align: center;
}

.searchPage__info > p {
    font-size: 14px;
}

.searchPage__info > h1 {
    text-align: center;
    margin-top: 20px;
}

.searchPage__info > button {
    border-radius: 20px;
    text-transform: inherit;
    margin: 20px;
    font-size: 20px;
    background-color: lavenderblush;
}

.searchPage__info > button:hover{
    background-color: lightblue;
}

.home__section {
    display: flex;
    flex-wrap: wrap;
    padding: 30px;
    flex: auto;
    margin-top: -10px;
    flex-basis: 50%;
    justify-content: space-evenly;
    align-content: center;
}


.fancy {
    width:100%;
    height: 10vw;
    position: flex;
    align-self: center;
}

.bott {
    padding: 20px;
    font-size: 12px;
}

.focus {
    border-radius: 20px;
    font-size: 14px;
    outline-width: 1;
    background-color: lavenderblush;
    margin: 5px;
    color: black;
}

#error {
    font-size: 20px;
}

#error > button {
    font-size: 16px;
    border-radius: 20px;
    background-color: lavenderblush;
    text-transform: inherit;
    margin-top: -4px;
    margin: 4px;
}

#error > button:hover {
    background-color: lightgreen;
}

#ret {
    position:flex;
    font-size: 14px;
    border-radius: 20px;
    background-color: lavenderblush;
    text-transform: inherit;
    margin-left: 1220px;
    right: 0px;
    margin-top: -20px;
    margin-bottom: 10px;
}

#ret:hover {
    background-color: salmon;
}
