.profile {
    display: flex;
    position: relative;
    margin: 20px;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    cursor: pointer;
    margin-bottom: -5px;
  }

.profile:hover {
    opacity: 0.9;
}

.profile > img {
    height: 300px;
    width: 300px;
    border-radius: 10px;
    overflow: hidden;
}

.profile__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 20px;
}

#profile__mail {
    position: absolute;
    top: 20px;
    right: 40px;
    color: black;
    font-size: 40px;
}

.profile__infoTop {
    width: 40vw;
}

.profile__infoTop > h3 {
    font-weight: 300;
    margin-top: 10px;
}

.profile__infoTop > p {
    margin-top: 10px;
    font-size: 13px;
    color: black;
  }
  
.profile__infoBottom {
    display: flex;
    justify-content: space-between;
}


.profile__reviews {
    display: flex;
    align-items: center;
    font-size: 14px;
}

.profile__price > p {
    text-align: right;
  }

.profile__price {
    position: absolute;
    bottom: 20px;
    right: 30px;
}

.profile__price > h2 {
    text-align: right;
    padding: 10px;
}

.reviews {
    display: flex;
    padding-top: -15px;
    margin: 20px;
}

.reviews > p > h5 {
    padding-left: 20px;
    padding-top: 10px;
    font-weight: 400;
    width: 80vw;
}

.reviews > p > h3 {
    font-weight: bolder;
    padding-left: 20px;
    font-size: 22px;
    padding-top: -10px;
}

.profilee > h2 {
    padding-left: 20px;
    padding-top: 20px;
    font-size: 25px;
}

#pic {
    padding: 10px;
}


#tu {
    padding: 10px;
    color: lightgreen;
    font-size: 40px;
    align-self: right;
}

#td {
    padding: 10px;
    color: red;
    font-size: 40px;
    align-self: right;
    margin-right: 50px;
}

.rts > button {
    border-radius: 20px;
    font-size: 14px;
    outline-width: 1;
    background-color: lavenderblush;
    margin: 15px;
    color: black;
    text-transform: inherit;
}


.pts {
    padding-top: 20px;
    font-size: 16px;
    font-weight: 300;
    margin-top: 8px;
    display: flex;  
    justify-content: space-evenly;
    text-transform: inherit;
}

.pts > button {
    border-radius: 20px;
    font-size: 14px;
    outline-width: 1;
    background-color: lavenderblush;
    margin: 5px;
    color: black;
    text-transform: inherit;
}

.bott {
    padding: 20px;
    font-size: 12px;
}

.fancy {
    width:100%;
    height: 10vw;
    position: flex;
    align-self: center;
}

#lan {
    margin-top: 30px;
}